.New{
    background: url(../../assets/background.jpg);
    min-height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: aliceblue;
    display: flex;
    flex-direction: column;
}

.New h1, .welcome-head .app-button{
    margin: 0
}

.welcome-head{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
    text-align: center;
    animation: welcomeText 2.5s linear;
}

@keyframes welcomeText{
    0%{
        opacity: 0;
    }

    100%{
        opacity: 1;
    }
}

.welcome-head .app-head{
    font-size: 2.8rem;
}

.welcome-head .app-button{
    transform: translateY(2rem);
}


@media only screen and (max-width: 768px) {
    .welcome-head .app-head{
        font-size: 2.4rem;
    }
}

.app-button a{
    text-decoration: none;
    color: #d6d6d6;
}

.app-button a:hover{
    color: #272727;
}

.redirection img{
    height: 250px;
}

.redirection{
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: bottomTop 3s linear forwards;
}

@keyframes bottomTop{
    0%{
        transform: translateY(12rem);
        opacity: 0;
    }
    100%{
        transform: translateY(2rem);
        opacity: 1;
    }
}