.app-button {
    margin-left: 0.5rem;
    padding: 0.6rem 1.6rem;
    border-radius: 3px;
    border: none;
    background: #3F51B5;
    color: #c7c7c7;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.6s ease;
}

.app-button:hover {
    background: #9aa5e2;
    color: #272727;
}

.App form{
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    margin: 0rem 1rem;
}

.app-head{
    color: #a6d6ff;
    font-size: 1.8rem;
    margin-top: 0;
    padding-top: 2rem;
}

.header-img{
    height: 15rem;
    width: 25rem;
}

.movie-details {
    animation: fadeIn 2s linear;
    margin-top: 2rem;
    color: #c7c7c7;
    text-align: center;
    width: 100%;
}

.movie-details .title{
    font-weight: bold;
    font-size: 2rem;
    margin-bottom: 0.6rem;
    padding: 0 5rem;
    color: #fff;
}

.movie-desc{
    display: flex;
    padding: 0 4rem;
}

.img-container{
    width: 40%;
}

.img-container > img{
    height: 90%;
    width: 80%;
    object-fit: cover;
    border: 7px solid #3a7aac;
    border-radius: 5px;
}

.movie-desc .desc{
    text-align: left;
    padding: 0 1rem;
    margin-bottom: 2rem;
    width: 70%;
}

.movie-desc .desc p{
    font-weight: bold;
    color: #1deeff;
}

.search-bar{
    width: 25rem;
    height: 2rem;
    font-size: 1rem;
    padding: 0.2rem 0.5rem;
    border-radius: 5px;
    border: none;
}


@media only screen and (max-width: 768px) {
    .search-bar{
        width: 15rem;
    }
    .header-img {
        height: 100%;
        width: 100%;
    }
    .movie-desc{
        flex-direction: column;
    }

    .img-container, .desc{
        width: inherit !important;
    }

    .movie-desc {
        padding: 0 1rem;
    }

}

.no-movie {
    height: 20rem;
    margin-top: 2rem;
    animation: fadeIn 0.7s linear;
}

@keyframes fadeIn {
    0% {
        opacity: 0
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 0;
    }
}


/* Loader */
.show{
    display: block !important;
}

.hide{
    display: none !important;
    animation: fadeOut 1s linear;
}

.loaderRoot{
    position: relative;
    display: none;
    width: 100%;
}

.showbox {
    position: absolute;
    top: 1rem;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 5%;
}


.loader {
    position: relative;
    margin: 0px auto;
    width: 50px;
}

.loader:before {
    content: '';
    display: block;
    padding-top: 100%;
}

.circular {
    animation: rotate 2s linear infinite;
    height: 100%;
    transform-origin: center center;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}


.path {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    animation:
        dash 1.5s ease-in-out infinite,
        color 6s ease-in-out infinite;
    stroke-linecap: round;
}

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes dash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }

    50% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -35;
    }

    100% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -124;
    }
}

@keyframes color {

    100%,
    0% {
        stroke: rgb(160, 241, 253);
    }

    40% {
        stroke: rgb(0, 204, 255);
    }

    66% {
        stroke: rgb(66, 176, 250);
    }

    80%,
    90% {
        stroke: rgb(164, 238, 255);
    }
}
/* End loader */